.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 50px;
  padding: 20px;
  color: white;
  display: -ms-flexbox;
  display: flex;
}

.App-title {
  font-size: 1.5em;
  -ms-flex: 1 1;
      flex: 1 1;
}

.App-header input {
  margin-left: 4px;
  width: 50px;
}

.App-intro {
  font-size: large;
}

.App-container {
	position: relative;
}

.App-info {
  position: absolute;
	right: 0;
  top: 0;
  display: -ms-flexbox;
  display: flex;
  max-width: 100%;
}

.App-info > * {
	border: 1px solid #333;
	padding: 8px;
	width: 150px;
  margin: 4px;
  background: white;
}

.App-info p {
  margin: 4px;
}

canvas {
  margin-top: 40px;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



